function BuyDisp({ product, createSale, children }) {
  const inStockPackages = product
    ? product.packages.filter((pkg) => pkg.quantity <= product.stockCount)
    : [];

  let [enabled, updateEnabled] = useState(false);
  let [selectedPackage, updateSelectedPackage] = useState();

  if (!product || product.stockCount == 0 || inStockPackages.length == 0) {
    return (
      <>
        <select>
          <option>OOS</option>
        </select>
        {children}
        <div className="pay">
          <button className="purchasebtn bold" disabled={true}>
            OOS
          </button>
        </div>
      </>
    );
  } else {
    return (
      <>
        {/* <h4>
          
        </h4> */}
        <select
          name="cars"
          id="cars"
          onChange={(ev) => {
            //Alert here

            updateSelectedPackage(ev.target.value);
          }}
        >
          {inStockPackages.map((pkg) => {
            return (
              <option value={`${pkg.quantity}:${pkg.price}`}>
                {" "}
                {pkg.quantity}
              </option>
            );
          })}
        </select>
        {children}
        <div className="pay">
          <button
            className="purchasebtn bold"
            onClick={() => {
              updateEnabled(true);

              let quantity = selectedPackage
                ? selectedPackage
                : `${inStockPackages[0].quantity}:${inStockPackages[0].price}`;
              createSale(product, quantity);
            }}
          >
            {selectedPackage
              ? `$${selectedPackage.split(":")[1]}`
              : `$${inStockPackages[0].price}`}
          </button>
        </div>
        {/* <button
          type="button"
          class="btn btnx"
          disabled={enabled}
          onClick={() => {
            updateEnabled(true);

            let quantity = selectedPackage
              ? selectedPackage
              : `${inStockPackages[0].quantity}:${inStockPackages[0].price}`;
            createSale(product, quantity);
          }}
        >
          Purchase
        </button> */}
      </>
    );
  }
}

