function Main() {
  const [currentView, updateCurrentView] = useState("normal");
  const [productData, updateProductData] = useState([]);
  const [product, updateProduct] = useState();
  const [quantity, updateQuantity] = useState();

  useEffect(() => {
    (async () => {
      let resp = await fetch(`${Config.endPoint}${Config.api.products}`);
      let json = await resp.json();

      let finalProductData = {};
      json.forEach((product) => {
        finalProductData[product.frontId] = product;
      });
      updateProductData(finalProductData);
    })();
  }, []);

  const _createSale = (selectedProduct, selectedQuantity) => {
    (async () => {
      updateProduct(selectedProduct);
      updateQuantity(selectedQuantity);
      updateCurrentView("sale");
    })();
  };

  const _cancelSale = () => {
    updateCurrentView("normal");
  };

  if (currentView == "sale") {
    return (
      <Checkout close={_cancelSale} product={product} quantity={quantity} />
    );
  } else {
    return <App productData={productData} createSale={_createSale} />;
  }
}

export default Main;
