import React, { useState, useEffect } from "react";
import { CardElement, useStripe, useElements } from "@stripe/react-stripe-js";
import logo from "./Asset 1.png";

import { FaTimes } from "react-icons/fa";
import "./checkout.scss";
import Config from "./Config";

export default function CheckoutForm({
  close,
  clientSecret,
  product,
  quantity,
}) {
  const [succeeded, setSucceeded] = useState(false);
  const [error, setError] = useState(null);
  const [processing, setProcessing] = useState("");
  const [disabled, setDisabled] = useState(true);
  const [email, updateEmail] = useState("");
  const [discount, updateDiscount] = useState(0);
  const [discountCode, updateDiscountCode] = useState("");
  const [discountMessage, updateDiscountMessage] = useState(
    "Ensure you see the updated value after discount on top before purchasing."
  );

  const stripe = useStripe();
  const elements = useElements();

  const cardStyle = {
    style: {
      base: {
        color: "#32325d",

        // fontFamily: "Arial, sans-serif",
        fontSmoothing: "antialiased",
        fontSize: "16px",
        "::placeholder": {
          color: "#32325d",
          padding: "1rem",
        },
      },
      invalid: {
        color: "#fa755a",
        iconColor: "#fa755a",
      },
    },
  };
  const handleChange = async (event) => {
    // Listen for changes in the CardElement
    // and display any errors as the customer types their card details
    setDisabled(event.empty);
    setError(event.error ? event.error.message : "");
  };

  const askFullfilment = async (paymentIntent) => {
    let paymentPayload = await fetch(
      `${Config.endPoint}${Config.api.fulfill}`,
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(paymentIntent),
      }
    );

    let json = await paymentPayload.json();

    if (json.success) {
      setError(null);
      setProcessing(false);
      setSucceeded(true);
    } else {
      setError(
        `Payment failed if you were charged please contact support for a refund!`
      );
      setProcessing(false);
    }
  };

  const checkDiscount = async () => {
    let discountPayload = await fetch(
      `${Config.endPoint}${Config.api.discount}`,
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ discount: discountCode }),
      }
    );

    let discountJSON = await discountPayload.json();

    if (discountJSON.percentage != 0 && !discountJSON.error) {
      updateDiscountMessage(
        `${discountCode} Applied for Discount of ${discountJSON.percentage}`
      );
      updateDiscount(discountJSON.percentage);
    }
  };

  const handleSubmit = async (ev) => {
    ev.preventDefault();
    setProcessing(true);

    let payload = await stripe.createPaymentMethod({
      type: "card",
      card: elements.getElement(CardElement),
      billing_details: {
        name: ev.target.name.value,
      },
    });

    if (payload.error) {
      setError(`Payment failed ${payload.error.message}`);
      setProcessing(false);
    } else {
      let paymentMethodId = payload.paymentMethod.id;
      //Send this payment method with the other details

      let paymentPayload = await fetch(`${Config.endPoint}${Config.api.pay}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          paymentMethodId,
          productId: product._id,
          quantity: parseInt(quantity.split(":")[0]),
          email,
          discount: discountCode,
        }),
      });

      let result = await paymentPayload.json();

      if (result.error) {
        setError(`Payment failed ${result.error.message}`);
        setProcessing(false);
      } else if (
        result.status == "requires_action" ||
        result.status == "requires_confirmation"
      ) {
        let confirmResult = await stripe.confirmCardPayment(
          result.client_secret
        );
        console.log(confirmResult);
        if (!confirmResult.error) {
          let finalData = await askFullfilment(confirmResult.paymentIntent);
        } else {
          setError(`Payment failed ${result.error.message}`);
          setProcessing(false);
        }
      } else {
        //Fullfillment Codde here

        let finalData = await askFullfilment(result);
      }
    }
  };

  return (
    <>
      <div className="container">
        <div className="loginwrapper">
          <div className="loginsection">
            <div className="logintext bold">Checkout</div>
            <div className="smtext">PLEASE CHECKOUT BELOW.</div>

            <div className="row flex-nowrap mb-lg-5">
              <div className="contents col-md-4">
                <img
                  className="shop"
                  src="https://dash.arrowproxies.com/assets/purchase.svg"
                  alt=""
                />

                <div className="productscontainer">
                  <div className="products">
                    <div className="productname bold">
                      {parseInt(quantity.split(":")[0])} {product.name}{" "}
                    </div>
                  </div>
                </div>

                <div className="amount">
                  <div className="subtotal">
                    Subtotal: <span>${parseInt(quantity.split(":")[1])}</span>
                  </div>

                  <div className="tax">
                    Discount:{" "}
                    <span id="total-discount">
                      ${discount * parseInt(quantity.split(":")[1]).toFixed(2)}
                    </span>
                  </div>

                  <div className="total bold">
                    Total:{" "}
                    <span id="total-price">
                      {" "}
                      $
                      {parseInt(quantity.split(":")[1]) -
                        discount * parseInt(quantity.split(":")[1])}
                    </span>
                  </div>
                </div>
              </div>
              <div className="customerdetails col-md-8">
                <div
                  className="close"
                  onClick={() => {
                    close();
                  }}
                >
                  <FaTimes size={"1em"} />
                </div>
                <form
                  id="payment-form"
                  className=""
                  onSubmit={handleSubmit}
                  style={{ width: "100%", marginTop: "2.75rem" }}
                >
                  <div className="mb-3 item">
                    <label htmlFor="address" className="bold">
                      Email
                    </label>
                    <input
                      type="text"
                      className="form-control"
                      placeholder={"Email"}
                      onChange={(e) => {
                        updateEmail(e.target.value);
                      }}
                    />
                    <p
                      style={{
                        textAlign: "left",
                        fontSize: "14px",
                        marginTop: "15px",
                        marginBottom: "15px",
                      }}
                    >
                      This is the email the product will be delivered to please
                      double check it.
                    </p>
                  </div>
                  <div className="mb-3 item">
                    <label htmlFor="address" className="bold">
                      Discount
                    </label>
                    <input
                      type="text"
                      className="form-control"
                      id="discountcode"
                      placeholder={"Discount Code"}
                      onChange={(e) => {
                        updateDiscountCode(e.target.value);
                      }}
                      onBlur={() => {
                        checkDiscount();
                      }}
                    />
                    <p
                      style={{
                        textAlign: "left",
                        fontSize: "14px",
                        marginTop: "15px",
                        marginBottom: "15px",
                      }}
                    >
                      {discountMessage}
                    </p>
                  </div>
                  <div
                    className="sr-field-error"
                    id="card-errors"
                    role="alert"
                  ></div>
                  <label
                    htmlFor="address"
                    className="bold"
                    style={{ marginBottom: "1rem" }}
                  >
                    Card Details
                  </label>
                  <CardElement
                    className="strp-input"
                    id="card-element"
                    options={cardStyle}
                    onChange={handleChange}
                  />
                  <button
                    disabled={processing || disabled || succeeded || !email}
                    className="paybtn bold"
                    id="submit"
                  >
                    <span id="button-text">
                      {processing ? (
                        <div className="spinner" id="spinner"></div>
                      ) : (
                        `Pay $${
                          parseInt(quantity.split(":")[1]) -
                          discount * parseInt(quantity.split(":")[1])
                        }`
                      )}
                    </span>
                  </button>
                  {/* Show any error that happens when processing the payment */}
                  {error && (
                    <div className="card-error" role="alert">
                      {error}
                    </div>
                  )}
                  {/* Show a success message upon completion */}
                  <p
                    className={
                      succeeded ? "result-message" : "result-message hidden"
                    }
                  >
                    Payment succeeded! ‚úîÔ∏è
                    <br />
                    üìß Your product will be delivered to the given email
                    shortly.
                  </p>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
